import { Component, Input, OnInit } from '@angular/core';
import { IOptionsPsLovCorporateTypeExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-lov/ps-dropdown-corporate-types/ps-dropdown-corporate-types.component.interface';
import { PsDropdownLovComponent } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-lov/ps-dropdown-lov.component';
import { IOptionsPsDropdownLov } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-lov/ps-dropdown-lov.component.interfaces';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IchangeValues } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';

@Component({
  selector: 'ps-lov-corporate-type',
  templateUrl: './ps-dropdown-corporate-types.component.html',
  styleUrls: ['./ps-dropdown-corporate-types.component.scss'],
})
export class PsDropdownCorporateTypesComponent extends PsDropdownLovComponent implements OnInit {

  @Input() options: IOptionsPsLovCorporateTypeExposed

  defaultSelectOptions: IOptionsPsDropdownLov = {
    lovTypeId: CommonBussinessConstant.CORPORATE_TYPE_LOV_TYPE_ID
  };
  constructor(commonProvService: PsCommonService, logger: LoggerService) {
    super(commonProvService, logger);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultSelectOptions, this.options, false);
    this.defaultSelectOptions.labelKey = this.commonProv.translate('corporate_type_key');
    this.defaultSelectOptions.placeHolder = this.commonProv.translate('select_corporate_type_key');
  }
  onChangeLov(values: IchangeValues) {
    this.onPsChange.emit(values);
  }

}
