import { Component, Input, OnInit } from '@angular/core';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsSelectDropdownComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-select/ps-select-dropdown/ps-select-dropdown.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IchangeValues, IOptionsPsSelectDropdown, IPsSelect } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from './../../../../commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsDropdownAssetsDescriptionExposed } from './ps-dropdown-assets-description.component.interfaces';

/**
 * @author G.Radwan
 * @since 02/03/2021
 *
 * <p> PsDropdownproductCategoriesComponent will fetch the list of product categories from server</p>
 */
@Component({
  selector: 'ps-dropdown-assets-description',
  templateUrl: './ps-dropdown-assets-description.component.html',
  styleUrls: ['./ps-dropdown-assets-description.component.scss'],
})
export class PsDropdownAssetDescriptionComponent extends PsSelectDropdownComponent implements OnInit {

  @Input() options: IOptionsPsDropdownAssetsDescriptionExposed = {};
  public defaultOptions: IOptionsPsSelectDropdown = {
    listOfOptions: [],
  };

  constructor(
    commonService: PsCommonService,
    logger: LoggerService,
    private omniPullService?: OmniPullService
  ) {
    super(commonService, logger);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultOptions, this.options, false);
    this.loadAssestsDescription();
  }

  /**
   * populating assets fetched from server
   */
  private async loadAssestsDescription() {

    //  const assestsList = await this.omniPullService.returnAssetsList({}).catch(error => {
    //   this.logger.error('Error: While fetching Product types in PsDropdownAssetDescriptionComponent : ', error);
    // });
    const assestsList = {
      gridModel: [{
      categoryCode: '1',
      description: 'asset 1'
    },{
      categoryCode: '2',
      description: 'asset 2'
    }
  ]}
    if (assestsList && assestsList.gridModel && assestsList.gridModel.length > 0) {
      for (const iterator of assestsList.gridModel) {
        const assest: IPsSelect = {
          itemValue: iterator.categoryCode,
          description: iterator.description,
          selectedObj: iterator
        };
        this.defaultOptions.listOfOptions.push(assest);
      }
    }
  }

  selectDropDownChange(values: IchangeValues) {
    this.onPsChange.emit(values);
  }
}
